import { useTranslation } from 'react-i18next'
import type { SupportedLanguage } from '@/i18n'
import { useLanguageStore } from '@/stores/languageStore'

/**
 * Hook
 */
export const useI18n = () => {
  const { t, i18n } = useTranslation()
  const {
    currentLanguage,
    availableLanguages,
    setLanguage,
    getLanguageName,
    getNativeLanguageName,
  } = useLanguageStore()

  /**
   * 
   */
  const translate = (key: string, options?: any) => {
    return t(key, options)
  }

  /**
   * 
   */
  const formatNumber = (value: number, options?: Intl.NumberFormatOptions) => {
    const locale =
      currentLanguage === 'zh-CN' ? 'zh-CN' : currentLanguage === 'ja-JP' ? 'ja-JP' : 'en-US'

    return new Intl.NumberFormat(locale, {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
      ...options,
    }).format(value)
  }

  /**
   * 
   */
  const formatCurrency = (value: number, currency: string = 'CNY') => {
    const locale =
      currentLanguage === 'zh-CN' ? 'zh-CN' : currentLanguage === 'ja-JP' ? 'ja-JP' : 'en-US'

    // 
    const defaultCurrency =
      currentLanguage === 'zh-CN' ? 'CNY' : currentLanguage === 'ja-JP' ? 'JPY' : 'USD'

    return new Intl.NumberFormat(locale, {
      style: 'currency',
      currency: currency || defaultCurrency,
    }).format(value)
  }

  /**
   * 
   */
  const formatPercent = (value: number, options?: Intl.NumberFormatOptions) => {
    const locale =
      currentLanguage === 'zh-CN' ? 'zh-CN' : currentLanguage === 'ja-JP' ? 'ja-JP' : 'en-US'

    return new Intl.NumberFormat(locale, {
      style: 'percent',
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
      ...options,
    }).format(value / 100)
  }

  /**
   * 
   */
  const formatDate = (date: Date | string | number, options?: Intl.DateTimeFormatOptions) => {
    const locale =
      currentLanguage === 'zh-CN' ? 'zh-CN' : currentLanguage === 'ja-JP' ? 'ja-JP' : 'en-US'

    const defaultOptions: Intl.DateTimeFormatOptions = {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    }

    return new Intl.DateTimeFormat(locale, { ...defaultOptions, ...options }).format(new Date(date))
  }

  /**
   * 
   */
  const formatTime = (date: Date | string | number, options?: Intl.DateTimeFormatOptions) => {
    const locale =
      currentLanguage === 'zh-CN' ? 'zh-CN' : currentLanguage === 'ja-JP' ? 'ja-JP' : 'en-US'

    const defaultOptions: Intl.DateTimeFormatOptions = {
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
    }

    return new Intl.DateTimeFormat(locale, { ...defaultOptions, ...options }).format(new Date(date))
  }

  /**
   * 
   */
  const formatDateTime = (date: Date | string | number, options?: Intl.DateTimeFormatOptions) => {
    const locale =
      currentLanguage === 'zh-CN' ? 'zh-CN' : currentLanguage === 'ja-JP' ? 'ja-JP' : 'en-US'

    const defaultOptions: Intl.DateTimeFormatOptions = {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    }

    return new Intl.DateTimeFormat(locale, { ...defaultOptions, ...options }).format(new Date(date))
  }

  /**
   * 2
   */
  const formatRelativeTime = (date: Date | string | number) => {
    const locale =
      currentLanguage === 'zh-CN' ? 'zh-CN' : currentLanguage === 'ja-JP' ? 'ja-JP' : 'en-US'

    const rtf = new Intl.RelativeTimeFormat(locale, { numeric: 'auto' })
    const now = new Date()
    const targetDate = new Date(date)
    const diffInSeconds = (targetDate.getTime() - now.getTime()) / 1000

    const intervals = [
      { unit: 'year' as const, seconds: 31536000 },
      { unit: 'month' as const, seconds: 2592000 },
      { unit: 'day' as const, seconds: 86400 },
      { unit: 'hour' as const, seconds: 3600 },
      { unit: 'minute' as const, seconds: 60 },
      { unit: 'second' as const, seconds: 1 },
    ]

    for (const interval of intervals) {
      const count = Math.floor(Math.abs(diffInSeconds) / interval.seconds)
      if (count >= 1) {
        return rtf.format(diffInSeconds < 0 ? -count : count, interval.unit)
      }
    }

    return rtf.format(0, 'second')
  }

  /**
   * 
   */
  const switchLanguage = (language: SupportedLanguage) => {
    setLanguage(language)
  }

  /**
   * RTL
   */
  const isRTL = () => {
    // LTRRTL
    return false
  }

  return {
    // 
    t: translate,

    // 
    currentLanguage,
    availableLanguages,
    switchLanguage,
    getLanguageName,
    getNativeLanguageName,
    isRTL,

    // 
    formatNumber,
    formatCurrency,
    formatPercent,
    formatDate,
    formatTime,
    formatDateTime,
    formatRelativeTime,

    // i18n
    i18n,
  }
}

export default useI18n
